/**
* SHARE
*/

.btn_post__share{
      width: 4.2rem;
      height: 4.2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      position: relative;
}

.btn_post__share:before{
      content: attr(data-tooltip);
      background: var(--text-300);
      color: var(--light);
      text-transform: uppercase;
      font-family: var(--ft-2);
      font-size: 1rem;
      padding: .2rem .75rem;
      border-radius: .3rem;
      top: -4rem;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 2;
      opacity: 0;
      visibility: hidden;
      transition: .2s ease-in-out;
}

.btn_post__share:after{
      width: 1rem;
      height: 1rem;
      background: var(--text-300);
      border-radius: .2rem;
      top: -2.75rem;
      left: calc(50% - .5rem);
      transform: rotate(45deg);
      opacity: 0;
      visibility: hidden;
      letter-spacing: .2rem;
      transition: .2s ease-in-out;
}

.btn_post__share:hover:before,
.btn_post__share:hover:after{
      opacity: 1;
      visibility: visible;
}

.btn_post__share.facebook{
      background: var(--facebook);
}

.btn_post__share.twitter{
      background: var(--twitter);
}

.btn_post__share.linkedin{
      background: var(--linkedin);
}

.btn_post__share.pinterest{
      background: var(--pinterest);
}

.btn_post__share.whatsapp{
      background: var(--whatsapp);
}

.btn_post__share *{
      color: var(--light);
      font-size: 2rem;
}