/**
* FRIEND MOBILE
*/

.friends_mobile__content{
      width: 100%;
      max-width: 38rem;
      margin: 0 auto;
      height: auto;
      position: relative;
}

.friend_mobile__row,
.friend_mobile__row a{
      width: 6rem;
      height: 7.5rem;
      flex-direction: column;
      align-items: center;
}

.slick-slide{
      margin: 0 1rem;
}

.slick-arrow{
      width: 3.6rem;
      height: 3.6rem;
      background: var(--bg);
      border-radius: 50%;
}

.slick-arrow:hover{
      background: var(--bg);
}

.slick-next{
      right: -5rem;
}

.slick-prev{
      left: -5rem;
}

.slick-next:before,
.slick-prev:before{
      color: transparent;
      width: 1rem;
      height: 1rem;
      border-left: solid .3rem var(--text-100);
      border-bottom: solid .3rem var(--text-100);
      top: 50%;
      left: 50%;
}

.slick-next:before{
      transform: translate(-65%, -50%) rotate(225deg);
}

.slick-prev:before{
      transform: translate(-35%, -50%) rotate(45deg);
}

.friend_mobile__avatar{
      width: 6rem;
      height: 6rem;
      border-radius: 50%;
      padding: .3rem;
      background: var(--text-100);
      position: relative;
}

.friend_mobile__avatar_img,
.mobile_avatar__gif{
      width: 8rem;
      height: 8rem;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(.68);
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      overflow: hidden;
}

.mobile_avatar__gif{
      background-color: #111;
      transform: translate(-50%, -50%) scale(1);
}

.friend_mobile__avatar_img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.friend_mobile__info{
      width: 100%;
      justify-content: center;
}

.friend_mobile__info span{
      text-transform: uppercase;
      font-size: .8rem;
      font-weight: 600;
      margin-top: .5rem;
      color: var(--text-100);
}

/**
* RESPONSIVE
*/

@media only screen and (max-width: 480px){
      .friends_mobile__content{
            max-width: 30rem;
      }
}