/**
* SOCIAL MENU
*/

.social_content{
      flex-direction: column;
      gap: 1rem;
}

.btn_social__media{
      height: 3.8rem;
      width: 100%;
      gap: 1rem;
      align-items: center;
}

.social_icon{
      width: 3.8rem;
      height: 3.8rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--bg);
      border-radius: .4rem;
      box-shadow: .5rem .5rem 10px rgba(0,0,0,.05);
}

.social_icon *{
      color: var(--light);
      font-size: 2rem;
}

.social_icon.facebook{
      background: var(--facebook);
}

.social_icon.twitter{
      background: var(--twitter);
}

.social_icon.instagram{
      background: var(--instagram);
}

.social_icon.youtube{
      background: var(--youtube);
}

.social_icon.tiktok{
      background: var(--tiktok);
}

.social_icon.tumblr{
      background: var(--tumblr);
}

.social_icon.discord{
      background: var(--discord);
}

.social_icon.twitch{
      background: var(--twitch);
}

.btn_social__media a{
      width: calc(100% - 4.8rem);  
      flex-grow: 1;
      line-height: 3.8rem;
      font-family: var(--ft-2);
      text-transform: uppercase;
      color: var(--text-200);
      font-size: 1.4rem;
      border-radius: .4rem;
      transition: .2s ease-in-out;
      position: relative;
}

.btn_social__media a:hover{
      background: var(--secundary);
      color: var(--light);
      box-shadow: .5rem .5rem 10px rgba(0,0,0,.1);
      padding-left: 2rem;
}

.btn_social__media span{
      font-size: 1.3rem;
      font-family: var(--ft-2);
}

.btn_social__media a i{
      width: 3rem;
      height: 100%;
      position: absolute;
      right: 0;
      top: 0;
      display: flex;
      align-items: center;
      overflow: hidden;
}

.btn_social__media a i *{
      font-size: 2rem;
      transform: scale(1);
      color: var(--text-100);
      opacity: .5;
}

.btn_social__media a:hover i *{
      opacity: 0;
}