/**
* RELATION
*/

.wrapper_relation{
      margin-top: 3rem;
      width: 100%;
      position: relative;
      padding-bottom: 5rem;
}

.relation_group{
      width: calc(100% - 4rem);
      margin-left: 2rem;
      border-radius: 2rem 2rem 1rem 1rem;
      background: var(--bg-100);
      box-shadow: 0 0 40px rgba(0,0,0,.1);
      padding-bottom: 2rem;
}

.relation_banner__group{
      width: 100%;
      height: 27rem;
      position: relative;
}

.relation_banner__img{
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 2rem 2rem 0 0;
      overflow: hidden;
}

.relation_banner__data{
      width: 100%;
      height: 100%;
      background: linear-gradient(-315deg, rgba(0,0,0,.4), transparent);
      position: relative;
      padding: 2rem;
      border-radius: 2rem 2rem 0 0;
      flex-direction: column;
      justify-content: flex-end;
}

.relation_banner__data h2{
      color: var(--secundary);
      text-transform: capitalize;
      font-weight: 600;
      text-shadow: .1rem .1rem 10px rgba(0,0,0,.2);
      font-size: 3rem;
      line-height: 2.5rem;
}

.relation_banner__data strong{
      color: var(--light);
      font-size: 2rem;
      font-weight: 500;
      opacity: .3;
      text-shadow: .1rem .1rem 2px rgba(0,0,0,.3);
}

.relation_banner__data p{
      color: var(--light);
      font-family: var(--ft-2);
      text-transform: uppercase;
}

.relation_banner__icon{
      position: absolute;
      width: 4.2rem;
      height: 4.2rem;
      top: 0;
      right: 2rem;
      background: var(--bg-100);
      box-shadow: 0 0 30px rgba(0,0,0,.1);
      align-items: center;
      justify-content: center;
      border-radius: .8rem;
      transform: translateY(-50%);
}

.relation_banner__icon *{
      font-size: 2.4rem;
      color: var(--secundary);
}

.relation_content{
      padding: 2rem 2rem 0 2rem;
}

.relation_content__header h3{
      font-size: 2.4rem;
      line-height: 2rem;
}

.relation_content__header p{
      font-family: var(--ft-2);
      color: var(--text-100);
}

.relation_content__description{
      margin: 2rem 0;
}

.relation_content__description p{
      font-family: var(--ft-2);
      line-height: 2.2rem;
      color: var(--text-200);
      white-space: pre-line;
}

.relation_content__action{
      border-top: solid .1rem var(--text-100);
      padding-top: 2rem;
      align-items: center;
      gap: 4rem;
}

.btn_relation{
      align-items: center;
      gap: 1rem;
      height: 4.2rem;
      background: var(--bg-100);
      padding: 0 3rem;
      border-radius: 1rem;
      transition: .2s ease-in-out;
}

.btn_relation:hover{
      box-shadow: .5rem .5rem 20px rgba(0,0,0,.08);
}

.btn_relation__icon *{
      color: var(--text-100);
      transitioN: .2s ease-in-out;
      padding-top: .3rem;
      font-size: 2rem;
}

.btn_relation span{
      font-family: var(--ft-2);
      color: var(--text-100);
      transition: .2s ease-in-out;
}

.btn_relation:hover .btn_relation__icon *{
      color: var(--secundary);
}

.btn_relation:hover span{
      color: var(--text-300);
}