/**
* INFODATA
*/

.infordata_content{
      width: 100%;
      padding: 5rem;
      background: var(--bg-100);
      border-radius: 0 0 1rem 1rem;
}

.infodata_col.data{
      justify-content: center;
}

.infodata_col__count{
      width: 33%;
      flex-direction: column;
      align-items: center;
      position: relative;
}

.infodata_col__count:before{
      width: .1rem;
      height: 2rem;
      background: var(--text-100);
      right: 0;
      top: calc(50% - 1rem);
}

.infodata_col__count:last-child:before{
      display: none;
}

.infodata_col__count strong{
      font-size: 3.5rem;
      line-height: 4rem;
}

.infodata_col__count span{
      text-transform: uppercase;
      font-family: var(--ft-2);
      font-weight: 500;
      color: var(--text-100);
      font-size: 1.3rem;
}

.infodata_col.data_user{
      flex-direction: column;
      align-items: center;
}

.infodata_col.data_user h1{
      font-size: 2.4rem;
      padding-top: 1rem;
}

.infodata_col.data_user p{
      text-transform: uppercase;
      font-weight: 600;
      color: var(--text-100);
      font-size: 1.3rem;
}

.infodata_col.data_location{
      align-items: center;
      gap: 1rem;
}

.location_icon{
      width: 4.5rem;
      height: 4.2rem;
      align-items: center;
      justify-content: center;
      position: relative;
}

.location_icon:before{
      width: 100%;
      height: 1.3rem;
      background: rgba(0,0,0,.05);
      border-radius: 50%;
      bottom: 0;
}

.location_icon span{
      position: relative;
      animation: pulse 1s ease-in-out infinite;
}

.location_icon span *{
      font-size: 3rem;
      color: var(--primary);
}

@keyframes pulse{
      0%{
            transform: translateY(0);
      }
      50%{
            transform: translateY(-1rem);
      }
      100%{
            transform: translateY(0);
      }
}

.location_data__info p{
      font-weight: 600;
      font-size: 1.6rem;
}

.location_data__info img{
      width: 3rem;
}

.friends_mobile{
      width: 100%;
      height: auto;
      margin-top: 3rem;
      display: none;
}

/**
* RESPONSIVE
*/


@media only screen and (max-width: 856px){
      .infordata_content{
            padding: 5rem 3rem;
      }
}

@media only screen and (max-width: 600px){
    .infodata_group.col-3{
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(2, 1fr);
    }

    .infodata_col{
        justify-content: center;
    }

    .infodata_col.data_user{
        grid-row: 1/2;
        margin-bottom: 2rem;
    }

    .infodata_col.data_location{
        display: none;
    }

    .friends_mobile{
        display: flex;
    }
}