/**
* EMPTY
*/

.page_empty{
      width: 100%;
      height: auto;
      padding: 8rem 6rem;
      flex-direction: column;
      align-items: center;
}

.empty_image{
      width: 20rem;
      height: 20rem;
      background: var(--bg);
      border-radius: 50%;
      align-items: center;
      justify-content: center;
}

.empty_image img{
      width: 16.5rem;
}

.page_empty h2{
      text-transform: uppercase;
      font-size: 2.4rem;
      margin-top: 2rem;
      color: var(--text-100);
}

.page_empty p{
      font-family: var(--ft-2);
      font-size: 1.8rem;
      color: var(--text-100);
      text-align: center;
}