/**
* CREDIT
*/

.credit_content{
      background: var(--bg-100);
      border-radius: .6rem;
      padding: 2rem;
      margin-bottom: 1.5rem;
}

.credit_content__box{
      margin-top: 2rem;
}

.credit_user__header{
      height: 4.6rem;
      align-items: center;
      gap: 1rem;
}

.credit_user__avatar{
      width: 4.6rem;
      height: 4.6rem;
      background: var(--text-100);
      border-radius: 50%;
      padding: .3rem;
      flex-shrink: 0;
}

.credit_user__avatar_img{
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      overflow: hidden;
}

.credit_user__data{
      height: 100%;
      flex: 1;
}

.credit_user__data strong{
      font-size: 1.4rem;
}

.credit_user__data p{
      font-family: var(--ft-2);
      font-size: 1.2rem;
      color: var(--text-200);
}

.credit_user__about{
      margin: 2rem 0 1rem 0;
}

.credit_user__about p{
      font-family: var(--ft-2);
      color: var(--text-200);
      line-height: 2.2rem;
}

.credit_user__about p span{
      font-family: var(--ft-2);
      color: var(--secundary);
      font-weight: 600;
}

.credit_user a{
      font-family: var(--ft-2);
      color: var(--text-100);
}

.credit_user a:hover{
      text-decoration: underline;
}