/**
* BEST VIDEO
*/

.best_video{
      background: var(--bg-100);
      border-radius: .6rem;
      padding: 2rem;
      margin-bottom: 1.5rem;
}

.best_video__content{
      margin-top: 2rem;
      padding-bottom: 2rem;
}

.best_video__box{
      width: 100%;
      height: 12.5rem;
      border-radius: 1.5rem;
      overflow: hidden;
}

.best_video__box iframe{
      border: none;
      outline: none;
}

.best_video__title{
      margin-top: 1rem;
}

.best_video__title p{
      font-weight: 500;
      font-size: 1.4rem;
      text-transform: capitalize;
}