/**
* PAGES 
*/

.wrapper_pages{
      flex-grow: 1;
      width: auto;
}

.navbar_pages{
      width: 100%;
      height: 8rem;
      background: var(--bg-100);
      border-radius: .6rem;
      padding: 0 1rem;
}

.pages_content__menu{
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: space-between;
}

.btn_pages{
      width: 25%;
      height: 100%;
      background: none;
      align-items: center;
      justify-content: center;
      color: var(--text-100);
      position: relative;
      transition: .2s;
}

.btn_pages:before{
      width: .1rem;
      height: 2rem;
      background: var(--text-100);
      top: calc(50% - 1rem);
      right: 0;
}

.btn_pages:last-child:before{
      display: none;
}

.btn_pages .btn_icon *{
      font-size: 2.4rem;
}

.btn_pages:hover,
.btn_pages.active{
      color: var(--text-300);
}

.btn_hover{
      width: 100%;
      height: .5rem;
      position: absolute;
      left: 0;
      bottom: 0;
      overflow: hidden;
}

.btn_hover:before{
      width: 100%;
      height: 300%;
      background: linear-gradient(var(--secundary) .5rem, var(--bg-100) .5rem, var(--bg-100) 1rem, var(--secundary) 1rem);
      transition: .3s ease-in-out;
      top: 100%;
      left: 0;
}

.btn_pages:hover .btn_hover:before,
.btn_pages.active .btn_hover:before{
      transform: translateY(-100%);
}

.pages_section__content{
      width: 100%;
      margin-top: 1.5rem;
      background: var(--bg-100);
      padding: 2rem 0;
      border-radius: .6rem;
      margin-bottom: 5rem;
}

.footer_main{
      background: var(--bg-100);
      border-radius: .6rem;
      flex-direction: column;
      align-items: center;
      padding: 2rem;
}

.footer_main h4{
      text-transform: uppercase;
      color: var(--text-100);
}

.footer_main span{
      font-size: 1.2rem;
      color: var(--text-100);
}

.footer_main p{
      font-size: 1.3rem;
      color: var(--text-100);
      margin-top: .5rem;
}