/**
* BANNER
*/

.banner_group,
.banner_content{
      width: 100%;
      height: 28rem;
      position: relative;
}

.banner_content__img{
      width: 100%;
      height: 100%;
      background: var(--bg-100);
      border-radius: 1rem 1rem 0 0;
      overflow: hidden;
      position: relative;
      align-items: center;
      justify-content: center;
}

.banner_content__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.btn_toggle__menu{
      width: 4rem;
      height: 4rem;
      position: absolute;
      top: 2rem;
      right: 2rem;
      border-radius: 50%;
      background: rgba(0,0,0,0);
      transition: .2s;
      display: none;
}

.btn_toggle__menu:hover{
      background: rgba(0,0,0,.5);
}

.btn_toggle__menu span{
      width: 2.4rem;
      height: .3rem;
      background: var(--bg-100);
      position: absolute;
      top: calc(50% - .15rem);
      left: calc(50% - 1.2rem);
      transition: .3s;
}

.btn_toggle__menu span:nth-of-type(1){
      transform: translateY(-.8rem);
}

.btn_toggle__menu span:nth-of-type(3){
      transform: translateY(.8rem);
      left: .8rem;
      width: 1.2rem;
}

.btn_toggle__menu:hover span:nth-of-type(3){
      left: calc(50% - 1.2rem);
      width: 2.4rem;
}

/**
* RESPONSIVE
*/

@media only screen and (max-width: 600px){
    .banner_group,
    .banner_content{
        height: 18rem;
    }
}