/**
* FRIENDS
*/

.wrapper_friends{
      background: var(--bg-100);
      padding: 2rem;
      border-radius: .6rem;
      position: relative;
      margin-bottoM: 1.5rem;
}

.friends_amount{
      width: 3rem;
      height: 3rem;
      position: absolute;
      top: 2.8rem;
      left: 9rem;
      align-items: center;
      justify-content: center;
      color: var(--secundary);
      font-weight: 500;
}

.friends_content__group{
      margin-top: 2rem;
}

.friend_row{
      height: 5rem;
      width: 100%;
      gap: 1rem;
      margin-bottoM: 2rem;
}

.friend_row__avatar{
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
      background: var(--text-100);
      position: relative;
      padding: .2rem;
      flex-shrink: 0;
}

.friend_row__avatar_img{
      width: 8rem;
      height: 8rem;
      background-color: #111;
      border-radius: 50%;
      overflow: hidden;
      transform: scale(.56);
      position: absolute;
      top: calc(50% - 4rem);
      left: calc(50% - 4rem);
      display: flex;
      align-items: center;
      justify-content: center;
}

.friend_row__avatar_img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

@keyframes move_img{
      100%{
            background-position: right top;
      }
}

.friend_row__data{
      flex: 1;
      height: 100%;
      align-items: center;
      gap: 1rem;
}

.friend_row__data_info{
      flex: 1;
      height: 100%;
}

.friend_row__data_info span{
      font-weight: 500;
      font-size: 1.4rem;
      text-transform: capitalize;
}

.friend_row__data_info p{
      font-family: var(--ft-2);
      font-size: 1.2rem;
      line-height: 1.8rem;
      color: var(--text-200);
}

.btn_friend__row{
      width: 3.6rem;
      height: 3.6rem;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background: var(--bg-100);
      box-shadow: 0 0 0 .1rem var(--text-100);
      border-radius: 30% 30% 30% 30% / 30% 30% 30% 30% ;
      color: var(--text-100);
      transition: .2s ease-in-out;
}

.btn_friend__row *{
      color: var(--text-100);
      font-size: 2.2rem;
      transition: .2s ease-in-out;
      position: relative;
}

.btn_friend__row:hover{
      background: var(--secundary);
      box-shadow: 0 0 0 .1rem transparent, .5rem .5rem 10px rgba(0,0,0,.1);
}

.btn_friend__row:hover *{
      color: var(--light);
}
