/**
* IMAGES
*/

.page_group__images{
      margin-top: 2rem;
}


.group_images__list{
      flex-direction: column;
}

.image_list__item{
      width: 100%;
      height: auto;
      margin-bottom: .2rem;
      position: relative;
      overflow: hidden;
}

.image_item__cover{
      width: 100%;
      height: 33rem;
      position: relative;
}

.image_item__cover_img{
      width: 100%;
      height: 100%;
      position: relative;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.image_item__cover_img:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.4);
      transition: .3s ease-in-out;
      opacity: 0;
}

.image_list__item:hover .image_item__cover_img:before{
      opacity: 1;
}

.image_item__cover_img img{
      min-height: 100%;
}

.btn_image__zoom{
      width: 5rem;
      height: 5rem;
      position: absolute;
      right: 0;
      bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--secundary);
      transform: translateX(100%);
      transition: .2s .3s cubic-bezier(.43,.12,.52,1.59);
      z-index: 2;
}

.btn_image__zoom *{
      color: var(--light);
      font-size: 2.4rem;
}

.image_list__item:hover .btn_image__zoom{
      transform: translateX(0);
}

.image_item__description{
      position: absolute;
      width: 100%;
      height: 3.2rem;
      bottom: 0;
      left: 0;
      padding: 0 1.5rem;
      transform: translateY(100%);
      transition: .2s ease-in-out; 
}

.image_item__description p{
      color: var(--light);
      font-family: var(--ft-2);
      line-height: 3.2rem;
      text-transform: capitalize;
}

.image_list__item:hover .image_item__description{
      transform: translateY(0);
}