/**
* HEADERPAGE
*/

.header_page__content{
      width: 100%;
      height: auto;
      justify-content: space-between;
      padding: 0 2rem;
}

.header_page__user{
      align-items: center;
      gap: 1rem;
}

.page_user__avatar{
      width: 4.4rem;
      height: 4.4rem;
      border-radius: 50%;
      background: var(--bg);
      box-shadow: 0 0 0 .3rem var(--text-100);
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.page_user__avatar img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.page_user__data{
      flex-direction: column;
}

.page_user__data strong{
      font-size: 1.4rem;
      font-weight: 600;
}

.page_user__data span{
      font-size: 1.2rem;
      font-weight: 500;
      color: var(--secundary);
}

.page_user__data p{
      font-family: var(--ft-2);
      font-size: 1.2rem;
      color: var(--text-200);
      font-weight: 600;
      text-transform: capitalize;
}