/**
* USER
*/

.user_content{
      width: 16rem;
      height: 18rem;
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 25%);
}

.user_avatar{
      width: 16rem;
      height: 18rem;
      background: var(--bg-100);
      position: relative;
      clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
}

.user_avatar__effect{
      width: 15rem;
      height: 17rem;
      position: absolute;
      background: var(--bg);
      top: .5rem;
      left: .5rem;
      clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
}

.user_avatar__effect:before{
      width: 200%;
      height: 200%;
      top: 50%;
      left: 50%;
      background: linear-gradient(var(--secundary) 50%, var(--bg) 50.5%);
      transform: translate(-50%, -50%) rotate(45deg);
}

.user_avatar__img{
      width: 14rem;
      height: 16rem;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      align-items: center;
      justify-content: center;
      clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
}

.user_avatar__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.user_data{
      position: absolute;
      top: 6rem;
      left: 110%;
}

.user_data p{
      white-space: nowrap;;
      color: var(--light);
      font-weight: 500;
      text-shadow: .1rem .1rem 3px rgba(0,0,0,.5);
}

.id_content{
      width: fit-content;
      line-height: 2.8rem;
      padding: 0 1.5rem;
      background: var(--bg-100);
      position: absolute;
      top: 6rem;
      left: 0;
      border-radius: 999px;
      transform: translateX(-5rem);
      font-family: var(--ft-2);
      font-weight: 500;
      letter-spacing: .05rem;
}

/**
* RESPONSIVE
*/

@media only screen and (max-width: 600px){
      .user_content{
            width: 12rem;
            height: 14rem;
      }
      
      .user_avatar{
            width: 12rem;
            height: 14rem;
      }
      
      .user_avatar__effect{
            width: 11rem;
            height: 13rem;
      }

      .user_avatar__img{
            width: 10rem;
            height: 12rem;
      }

      .user_data{
            top: 3rem;
      }

      .id_content{
            transform: translateX(-3.5rem);
      }
}