/**
* SOCIAL MOBILE
*/

.social_mobile{
      width: 3.8rem;
      height: auto;
      padding: .4rem;
      background: rgba(255,255,255,.3);
      backdrop-filter: blur(15px);
      box-shadow: .7rem .7rem 10px rgba(0,0,0,.1);
      border-radius: 999px;
      position: absolute;
      top: 3rem;
      left: 1rem;
      flex-direction: column;
      gap: 0;
      transition: .5s ease-in-out;
      display: none;
}

.btn_toogle_social{
      width: 3rem;
      height: 3rem;
      border-radius: 50%;
      background: var(--bg);
      position: relative;
}

.btn_toogle_social span{
      width: 2rem;
      height: .2rem;
      position: absolute;
      top: calc(50% - .1rem);
      left: calc(50% - 1rem);
      background: var(--text-300);
      transition: .3s ease-in-out;
}

.btn_toogle_social span:nth-of-type(1){
      transform: translateY(-.5rem);
}

.btn_toogle_social span:nth-of-type(3){
      transform: translateY(.5rem);
}

.btn_toogle_social.active span:nth-of-type(1),
.btn_toogle_social.active span:nth-of-type(2){
      width: 1.6rem;
      left: calc(50% - .8rem);
      transform: translateY(0) rotate(45deg);
}

.btn_toogle_social.active span:nth-of-type(3){
      width: 1.6rem;
      left: calc(50% - .8rem);
      transform: translateY(0) rotate(-45deg);
}

.social_mobile__items{
      flex-direction: column;
      gap: .4rem;
      display: flex;
      width: 100%;
      height: 0;
      overflow: hidden;
      transition: .5s ease-in-out;
}

.social_mobile__items.active{
      height: auto;
      margin-top: .4rem;
}

.social_mobile__items a{
      width: 3rem;
      height: 3rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--text-200);
      border-radius: 50%;
}

.social_mobile a *{
      color: var(--light);
      font-size: 1.4rem;
}

.social_mobile a.facebook{
      background: var(--facebook);
}

.social_mobile a.twitter{
      background: var(--twitter);
}

.social_mobile a.instagram{
      background: var(--instagram);
}

.social_mobile a.youtube{
      background: var(--youtube);
}

.social_mobile a.tiktok{
      background: var(--tiktok);
}

.social_mobile a.tumblr{
      background: var(--tumblr);
}

.social_mobile a.discord{
      background: var(--discord);
}

.social_mobile a.twitch{
      background: var(--twitch);
}

/**
* RESPONSIVE
*/

@media only screen and (max-width: 856px){
      .social_mobile{
          display: flex;
      }
  }