/**
* ONLINE FRIEND
*/

.online_friend{
      position: absolute;
      width: 1.6rem;
      height: 1.6rem;
      background: var(--bg-100);
      border-radius: 50%;
      padding: .3rem;
      right: -.2rem;
      bottom: 0;
}

.friend_mobile__avatar .online_friend{
      bottom: 4.1rem;
}

.dot_frient{
      width: 1rem;
      height: 1rem;
      background: var(--text-200);
      border-radius: 50%;
}

.dot_frient.green{
      background: var(--green);
}

.dot_frient.red{
      background: var(--red);
}