/**
* MODAL
*/

.modal_content{
      position: fixed;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.8);
      display: grid;
      place-content: center;
      width: 100%;
      height: 100vh;
      z-index: 10;
      opacity: 0;
      visibility: hidden;
}

.modal_content.show{
      animation: show_modal .3s ease-in-out forwards;
}

@keyframes show_modal{
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.modal_inner{
      width: fit-content;
      height: fit-content;
      max-height: 80vh;
      align-items: center;
      justify-content: center;
      opacity: 0;
      transition: .5s ease-in-out;
}

.modal_content.show .modal_inner{
      opacity: 1;
}

.modal_inner img{
      max-width: 86vw;
      max-height: 80vh;
}

.close_modal{
      position: absolute;
      bottom: 0;
      right: 0;
      width: 5rem;
      height: 5rem;
      background: var(--secundary);
      align-items: center;
      justify-content: center;
}

.close_modal *{
      color: var(--text-300);
      font-size: 2rem;
}