/**
* ABOUT
*/

.about_description{
      padding: 1rem 0;
}

.about_description p{
      font-family: var(--ft-2);
      color: var(--text-200);
      line-height: 2.2rem;
      white-space: pre-line;
}

.about_media__menu{
      width: 100%;
      height: auto;
      align-items: center;
      justify-content: space-between;
}

.about_media__menu a{
      width: 7rem;
      height: 2.6rem;
      line-height: 2.6rem;
      text-align: center;
      text-transform: uppercase;
      background: var(--secundary);
      box-shadow: -.2rem -.2rem 10px rgba(0,0,0,.03), .5rem .5rem 20px rgba(0,0,0,.03);
      border-radius: .4rem;
      position: relative;
      overflow: hidden;
}

.about_media__menu a:before{
      width: 200%;
      height: 100%;
      top: 0;
      left: 0;
      background: linear-gradient(90deg, var(--text-300) 50%, var(--bg-100) 50%);
      transform: translateX(-100%);
      transition: .3s ease-in-out;
}

.about_media__menu a:hover:before{
      transform: translateX(0);
}

.about_media__menu a span{
      font-size: 1.4rem;
      color: var(--light);
      font-family: var(--ft-2);
      position: relative;
      z-index: 1;
}

.about_social__media{
      margin-top: 2rem;
}

/**
* RESPONSIVE
*/

@media only screen and (max-width: 856px){
    .about_content{
        display: none;
    }
}