/**
* ONLINEUSER
*/

.online_user{
      width: fit-content;
      height: 2.6rem;
      padding: .6rem;
      background: rgba(0,0,0,.5);
      gap: 1rem;
      border-radius: 999px;
      align-items: center;
      margin-top: .5rem;
}

.online_user .dots{
      flex-grow: 1;
      widtH: 1.4rem;
      height: 1.4rem;
      border-radius: 50%;
}

.online_user .dots.red{
      background: var(--red);
}

.online_user .dots.green{
      background: var(--green);
}

.online_user span{
      white-space: nowrap;
      color: var(--light);
      padding-right: .5rem;
      font-size: 1.2rem;
}