/**
* VIDEOS
*/

.wrapper_video__group{
      margin-top: 2rem;
}

.video_row{
      width: 100%;
      height: auto;
      padding-bottom: 7rem;
      margin-bottom: 2rem;
      position: relative;
}

.video_row:before{
      width: calc(100% - 6rem);
      height: .1rem;
      left: 3rem;
      background: var(--text-100);
      bottom: 0;
}

.video_youtube__box{
      width: 100%;
      height: 29.4rem;
      background: #000;
}

.video_youtube__box iframe{
      border: none;
      outline: none;
}

.video_description{
      width: calc(100% - 6rem);
      height: 5.8rem;
      padding: 1rem;
      background: var(--bg-100);
      box-shadow: 0 0 40px rgba(0,0,0,.1);
      border-radius: 1rem;
      gap: 1rem;
      align-items: center;
      position: absolute;
      left: 3rem;
      bottom: 7rem;
      transform: translateY(50%);
      transition: .3s cubic-bezier(0.65, 0, 0.35, 1);
}

.video_row:hover .video_description{
      opacity: 0;
      visibility: hidden;
      bottom: 0;
      transform: translateY(0);
}

.video_icon{
      width: 3.4rem;
      height: 3.4rem;
      background: var(--bg);
      align-items: center;
      justify-content: center;
      border-radius: 50%;
}

.video_icon *{
      color: var(--youtube);
      font-size: 2rem;
}

.video_title{
      flex-grow: 1;
}

.video_title p{
      font-family: var(--ft-2);
      color: var(--text-200);
      text-transform: capitalize;
}