/**
* POSTS
*/

.card_post{
      width: 100%;
      height: auto;
      margin-bottom: 2rem;
}

.card_post__box{
      width: 100%;
      margin-top: 2rem;
}

.card_post__cover{
      width: 100%;
      height: 30rem;
      overflow: hidden;
      align-items: center;
      justify-content: center;
}

.card_post__cover img{
      height: auto;
      min-height: 100%;
}

.card_post__content{
      position: relative;
      width: calc(100% - 4rem);
      height: auto;
      padding: 1.5rem;
      background: var(--bg-100);
      transform: translate(2rem, -4rem);
      border-radius: 1rem;
      box-shadow: 0 0 50px rgba(0,0,0,.1);
}

.card_post__title{
      width: 100%;
      height: auto;
}

.card_post__title h2{
      font-size: 1.8rem;
      font-weight: 600;
      text-transform: capitalize;
}

.card_post__description{
      width: 100%;
      height: auto;
      margin-top: 1.5rem;
}

.card_post__description p{
      font-family: var(--ft-2);
      line-height: 2.2rem;
      color: var(--text-200);
      white-space: pre-line;
}

.card_post__action{
      margin-top: 1.5rem;
      justify-content: space-between;
      border-top: solid .1rem var(--text-100);
      padding-top: 2rem;
      align-items: center;
}

.post_action__share{
      position: relative;
}

.post_share__menu{
      position: absolute;
      top: -8.7rem;
      left: -4rem;
      background: var(--bg-100);
      height: 6.5rem;
      align-items: center;
      gap: 1rem;
      padding: 0 1.15rem;
      width: fit-content;
      border-radius: 999px;
      box-shadow: 0 0 40px rgba(0,0,0,.1);
      transform: translateY(-60%);
      opacity: 0;
      visibility: hidden;
}

.post_share__menu.show{
      animation: share_bounce .2s linear forwards;
}

@keyframes share_bounce {
      0%{
            opacity: 0;
            visibility: hidden;
            transform: translateY(-60%);
      }
      80%{
            opacity: 1;
            visibility: visible;
            transform: translateY(20%);
      }
      100%{
            opacity: 1;
            visibility: visible;
            transform: translateY(0);
      }
}

.btn_share{
      height: 4rem;
      align-items: center;
      justify-content: center;
      gap: 1rem;
      padding: 0 4rem;
      border-radius: 1rem;
      background: var(--bg-100);
      transition: .2s ease-in-out;
}

.btn_share:hover,
.btn_share.active{
      box-shadow: .5rem .5rem 20px rgba(0,0,0,.08);
}

.share_post__icon *{
      color: var(--text-100);
      font-size: 2rem;
      padding-top: .3rem;
      transition: .2s ease-in-out;
}

.btn_share:hover .share_post__icon *,
.btn_share.active .share_post__icon *{
      color: var(--secundary);
}

.btn_share span{
      font-family: var(--ft-2);
      color: var(--text-100);
      transition: .2s ease-in-out;
}

.btn_share:hover span,
.btn_share.active span{
      color: var(--text-300);
}

.card_post__at{
      gap: .75rem;
      align-items: center;
}

.post_at__icon *{
      fill: var(--text-100);
      color: var(--text-100);
      font-size: 1.75rem;
}

.card_post__at span{
      font-size: 1.4rem;
      color: var(--text-100);
      font-family: var(--ft-2);
}